/* global styles */
@import '../../App.scss';

.downloadResumeButton {
    //TODO: update download resume button to look nicer
    background-image: $light-blue-solid;
    color: $text-color-white;
    font-size: 16px;
    border-radius: 10%;
    &:hover {
        box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 25px 0 rgba(0,0,0,0.19);
        background-image: linear-gradient(#051726, #051726);
        cursor: pointer;
    }
}

/* MEDIA QUERIES */

//Non-mobile devices
@media (min-width: 1024px) {
    .resumeDownloadLabel {
        display: none;
    }
}

//Mobile devices

//Download not working on mobile devices right now
@media (max-width:1023px) {
    .downloadResumeButton {
        display: none;
    }
}


@media (min-width: 320px) and (max-width: 768px) {
    .resumeDownloadLabel {
        font-size: 16;
    }
    .resumeHeader {
        text-align: center;
        font-size: 20px;
    }
    .downloadButtonContainer {
        display: flex;
        justify-content: center;
    }
    .resumeDownloadLabel {
        text-align: center;
        white-space: pre-line;
        font-size: 12px;
        font-weight: normal;
    }
}

